.errand-item {
  display: flex;
  flex-direction: row;
  margin-bottom: 20px;
  justify-content: left;
  align-items: center;
  align-content: auto;
  overflow: visible;
  height: fit-content;
  width: fit-content;
  min-height: 50px;
  min-width: 40%;
  max-width: 40%;
  word-break: break-all;
  padding-right: 1%;
  color: #040720;
  font-family: 'Courier New', Courier, monospace;
  font-weight: bold;
  gap: 1rem;
}
.errand-item__content {
  display: flex;
  flex-direction: row;
  width: 100%;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 1rem;
}

.errand-item__actions {
  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  align-items: flex-end;
  justify-content: flex-end;
  width: fit-content;
}

.errand-item__edit-modal {
  display: flex;
  justify-content: center;
  transform: translate(0, 50%);
  background-color: orange;
}

.errand-item__delete-modal {
  display: flex;
  flex-direction: row;
  justify-content: center;
  transform: translate(0, 50%);
}
